/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import { useSelect } from '@safe-wordpress/data';
import { _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import { filter } from 'lodash';
import { PresetAlternative } from '@nab/components';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { ExperimentEditProps } from '@nab/types';

// NOTE. Dependency loop with @nab package.
const NAB_EDITOR = 'nab/editor';

/**
 * Internal dependencies
 */
import type { ControlAttributes, AlternativeAttributes } from '../types';

export const Alternative = ( {
	attributes: { templateId, name },
	disabled,
	experimentType,
	setAttributes,
}: ExperimentEditProps< AlternativeAttributes > ): JSX.Element => {
	const postType = usePostType();
	return (
		<PresetAlternative
			experimentType={ experimentType }
			collection={ postType }
			value={ templateId }
			onChange={ ( t ) =>
				t
					? setAttributes( { templateId: t.value, name: t.label } )
					: undefined
			}
			disabled={ disabled }
			labels={ {
				selection: name,
				selectAction: _x(
					'Please select a template',
					'user',
					'nelio-ab-testing'
				),
				loading: _x( 'Loading templates', 'text', 'nelio-ab-testing' ),
				empty: _x(
					'No templates available',
					'text',
					'nelio-ab-testing'
				),
				unknownOption: _x(
					'Template not found',
					'text',
					'nelio-ab-testing'
				),
			} }
		/>
	);
};

// =====
// HOOKS
// =====

const usePostType = () =>
	useSelect( ( select ) => {
		const { getAlternative } = select( NAB_EDITOR );
		const control = getAlternative< ControlAttributes >( 'control' );

		const { getKindEntities } = select( NAB_DATA );
		const postTypes = filter( getKindEntities(), { kind: 'entity' } );

		return control?.attributes.postType || postTypes[ 0 ]?.name;
	} );
