/**
 * WordPress dependencies
 */
import { addFilter } from '@safe-wordpress/hooks';
import { sprintf, _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import { isObject, values } from 'lodash';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { Dict, ExperimentType } from '@nab/types';

/**
 * Internal dependencies
 */
import icon from './icon.svg';
import { Original } from './components/original';
import { Alternative } from './components/alternative';

import type { ControlAttributes, AlternativeAttributes } from './types';

export const name = 'nab/template';

export const settings: Omit<
	ExperimentType< ControlAttributes, AlternativeAttributes >,
	'name'
> = {
	category: 'global',
	title: _x( 'Template', 'text (experiment name)', 'nelio-ab-testing' ),
	description: _x(
		'Test different templates on your site',
		'user',
		'nelio-ab-testing'
	),
	icon,
	supports: {
		presetAlternatives: ( select, collection ) => {
			const { getTemplates } = select( NAB_DATA );
			const templatesByPostType = getTemplates();

			if ( ! templatesByPostType ) {
				return false;
			} //end if

			if ( 'nab-check-availability' === collection ) {
				return values( templatesByPostType ).some(
					( templates ) => 2 <= templates.length
				);
			} //end if

			const postTypeTemplates = templatesByPostType[ collection ] || [];
			return postTypeTemplates.map( ( template ) => ( {
				value: template.id,
				label: template.name,
			} ) );
		},
	},
	help: {
		original: _x(
			'Split tests of page templates help you to improve the conversion rate of your site by switching the appearance of the post types that use a certain template. The control version (commonly known as the A version) are those elements that use the specified template.',
			'user',
			'nelio-ab-testing'
		),
		alternative: _x(
			'You can test the selected template against one or more alternative templates from those available in your WordPress site.',
			'user',
			'nelio-ab-testing'
		),
	},
	defaults: {
		original: {
			postType: '',
			name: '',
		},
		alternative: {
			name: '',
		},
	},
	views: {
		original: Original,
		alternative: Alternative,
	},
};

// =======
// FILTERS
// =======

addFilter(
	'nab.isTestedElementInvalid',
	'nab/templateTestedElementMightBeInvalid',
	( error, testedElement, experimentType ) => {
		if ( ! isControlOfType( experimentType, testedElement ) ) {
			return error;
		} //end if

		if ( ! testedElement.templateId ) {
			return _x(
				'Test doesnt specify the tested template',
				'text',
				'nelio-ab-testing'
			);
		} //end if

		return false;
	}
);

addFilter(
	'nab.isAlternativeInvalid',
	'nab/template/isAlternativeInvalid',
	( error, alternative, alternativeLetter, experimentType ) => {
		if ( ! isAlternativeOfType( experimentType, alternative ) ) {
			return error;
		} //end if

		if ( ! alternative.templateId ) {
			return sprintf(
				/* translators: letter of the variant */
				_x(
					'Please select a template in variant %s',
					'user',
					'nelio-ab-testing'
				),
				alternativeLetter
			);
		} //end if

		return false;
	}
);

// =======
// HELPERS
// =======

const isControlOfType = ( type: string, alt: Dict ): alt is ControlAttributes =>
	isObject( alt ) && name === type;

const isAlternativeOfType = (
	type: string,
	alt: Dict
): alt is AlternativeAttributes => isObject( alt ) && name === type;
