/**
 * WordPress dependencies
 */
import { addFilter } from '@safe-wordpress/hooks';
import { _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import { isObject } from 'lodash';
import { DefaultAlternative as Alternative } from '@nab/components';
import { createConversionAction } from '@nab/conversion-actions';
import type { ExperimentType } from '@nab/types';

/**
 * Internal dependencies
 */
import icon from './icon.svg';
import { Original } from './components/original';

import type { ControlAttributes, AlternativeAttributes } from './types';

export const name = 'nab/wc-product';

export const settings: Omit<
	ExperimentType< ControlAttributes, AlternativeAttributes >,
	'name'
> = {
	category: 'woocommerce',
	title: _x(
		'WooCommerce Product',
		'text (experiment name)',
		'nelio-ab-testing'
	),
	shortTitle: _x( 'Product', 'text (experiment name)', 'nelio-ab-testing' ),
	description: _x(
		'Test alternative versions of a product name, price, featured image, and short description',
		'user',
		'nelio-ab-testing'
	),
	icon,
	supports: {
		alternativeApplication: true,
		alternativeEdition: 'external',
		automaticGoalSetup: ( control ) => {
			return [
				createConversionAction( 'nab/wc-order', {
					type: 'product-selection',
					value: {
						type: 'some-products',
						value: {
							type: 'product-ids',
							mode: 'and',
							productIds: control.postId
								? [ control.postId ]
								: [],
						},
					},
				} ),
			];
		},
		postTypes: 'product',
	},
	help: {
		original: _x(
			'Product split tests help you to improve the interest your customers show in your products by creating one or more variants of the name, pricing, short description, and image of a WooCommerce Product. The control version (commonly known as the A version) is the already-existing product you want to test.',
			'user',
			'nelio-ab-testing'
		),
		alternative: _x(
			'Here you can create and edit one or more variants of your tested product. By default, each variant you create will use the products current values.',
			'user',
			'nelio-ab-testing'
		),
	},
	defaults: {
		original: {
			postId: 0,
			postType: 'product',
		},
		alternative: {
			name: '',
			postId: 0,
		},
	},
	views: {
		original: Original,
		alternative: Alternative,
	},
};

// =======
// FILTERS
// =======

addFilter(
	'nab.isTestedElementInvalid',
	'nab/woocommerceProduct/isTestedElementInvalid',
	( error, testedElement, experimentType ) => {
		if ( ! isControlOfType( experimentType, testedElement ) ) {
			return error;
		} //end if

		if ( ! testedElement.postId ) {
			return _x(
				'Please select the product you want to test',
				'user',
				'nelio-ab-testing'
			);
		} //end if

		return false;
	}
);

// =======
// HELPERS
// =======

const isControlOfType = (
	type: string,
	alt: unknown
): alt is ControlAttributes => isObject( alt ) && name === type;
