/**
 * WordPress dependencies
 */
import { addFilter } from '@safe-wordpress/hooks';
import { sprintf, _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import { isObject } from 'lodash';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { ExperimentType } from '@nab/types';

/**
 * Internal dependencies
 */
import icon from './icon.svg';
import { Original } from './components/original';
import { Alternative } from './components/alternative';

import type { ControlAttributes, AlternativeAttributes } from './types';

export const name = 'nab/theme';

export const settings: Omit<
	ExperimentType< ControlAttributes, AlternativeAttributes >,
	'name'
> = {
	category: 'global',
	title: _x( 'Theme', 'text (experiment name)', 'nelio-ab-testing' ),
	description: _x(
		'Test different themes on your site',
		'user',
		'nelio-ab-testing'
	),
	icon,
	supports: {
		alternativeApplication: true,
		scope: 'custom',
		presetAlternatives: ( select, collection ) => {
			const { getThemes } = select( NAB_DATA );
			const themes = getThemes();

			if ( ! themes ) {
				return false;
			} //end if

			if ( 'nab-check-availability' === collection ) {
				return true;
			} //end if

			return themes.map( ( theme ) => ( {
				value: theme.id,
				label: theme.name,
			} ) );
		},
	},
	help: {
		original: _x(
			'Split tests of WordPress themes help you to improve the conversion rate of your site by showing different themes to different visitors. The control version (commonly known as the A version) uses your current theme.',
			'user',
			'nelio-ab-testing'
		),
		alternative: _x(
			'You can test your active theme against one or more alternative themes from those installed in your WordPress site.',
			'user',
			'nelio-ab-testing'
		),
	},
	defaults: {
		original: {},
		alternative: {
			name: '',
			themeId: '',
		},
	},
	views: {
		original: Original,
		alternative: Alternative,
	},
};

// =======
// FILTERS
// =======

addFilter(
	'nab.isTestedElementInvalid',
	'nab/themeTestedElementShouldNotBeInvalid',
	( error, _, experimentType ) => ( name === experimentType ? false : error )
);

addFilter(
	'nab.isAlternativeInvalid',
	'nab/theme/isAlternativeInvalid',
	( error, alternative, alternativeLetter, experimentType ) => {
		if ( ! isAlternativeOfType( experimentType, alternative ) ) {
			return error;
		} //end if

		if ( ! alternative.themeId ) {
			return sprintf(
				/* translators: letter of the variant */
				_x(
					'Please select a theme in variant %s',
					'user',
					'nelio-ab-testing'
				),
				alternativeLetter
			);
		} //end if

		return false;
	}
);

// =======
// HELPERS
// =======

const isAlternativeOfType = (
	type: string,
	alt: unknown
): alt is AlternativeAttributes => isObject( alt ) && name === type;
