/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import { SelectControl } from '@safe-wordpress/components';
import { useDispatch, useSelect } from '@safe-wordpress/data';
import { _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import { filter } from 'lodash';
import { PresetAlternative } from '@nab/components';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import { isEmpty, omitUndefineds } from '@nab/utils';
import type {
	Alternative as AlternativeType,
	ExperimentEditProps,
	Maybe,
} from '@nab/types';

// NOTE. Dependency loop with @nab package.
const NAB_EDITOR = 'nab/editor';

/**
 * Internal dependencies
 */
import './style.scss';
import type { ControlAttributes, AlternativeAttributes } from '../../types';

export const Original = (
	props: ExperimentEditProps< ControlAttributes >
): JSX.Element => {
	const { experimentType } = props;
	const { attributes } = props;
	const { templateId, name } = attributes;

	const postTypes = usePostTypes();
	const setAttributes = useTemplateSetter(
		templateId ?? '',
		props.setAttributes
	);

	const loading = _x( 'Loading', 'text', 'nelio-ab-testing' );
	const postType = attributes.postType || postTypes[ 0 ]?.name;
	const selectablePostTypes = isEmpty( postTypes )
		? [ { value: '', label: loading } ]
		: postTypes.map( ( type ) => ( {
				value: type.name,
				label: type.labels.singular_name,
		  } ) );

	return (
		<div className="nab-template-original-alternative">
			<SelectControl
				value={ postType }
				options={ selectablePostTypes }
				disabled={ !! templateId || isEmpty( postTypes ) }
				onChange={ ( value ) =>
					setAttributes( {
						templateId: undefined,
						name: '',
						postType: value,
					} )
				}
			/>
			<PresetAlternative
				experimentType={ experimentType }
				collection={ postType }
				value={ templateId }
				onChange={ ( template ) =>
					template
						? setAttributes( {
								templateId: template.value,
								name: template.label,
								postType,
						  } )
						: undefined
				}
				disabled={ isEmpty( postTypes ) }
				labels={ {
					selection: name,
					selectAction: _x(
						'Select a template',
						'user',
						'nelio-ab-testing'
					),
					loading: _x(
						'Loading templates',
						'text',
						'nelio-ab-testing'
					),
					empty: _x(
						'No templates available',
						'text',
						'nelio-ab-testing'
					),
					unknownOption: _x(
						'Template not found',
						'text',
						'nelio-ab-testing'
					),
				} }
			/>
		</div>
	);
};

// =====
// HOOKS
// =====

const usePostTypes = () =>
	useSelect( ( select ) =>
		filter( select( NAB_DATA ).getKindEntities(), { kind: 'entity' } )
	);

const useTemplateSetter = (
	templateId: string,
	setAttributes: ( attrs: Partial< ControlAttributes > ) => void
) => {
	const { setAlternative } = useDispatch( NAB_EDITOR );
	const alternative = useSelect(
		( select ) => select( NAB_EDITOR )?.getAlternatives()[ 1 ]
	) as Maybe< AlternativeType< AlternativeAttributes > >;

	if ( !! templateId ) {
		return setAttributes;
	} //end if

	return ( attrs: Partial< ControlAttributes > ): void => {
		setAttributes( attrs );
		if ( alternative ) {
			setAlternative( alternative.id, {
				...alternative,
				attributes: omitUndefineds( {
					...alternative.attributes,
					...attrs,
				} ),
			} );
		} //end if
	};
};
