/**
 * External dependencies
 */
import { registerExperimentType } from '@nab/experiments';
import type { ExperimentType } from '@nab/types';

/**
 * Internal dependencies
 */
import * as css from './css';
import * as customPostType from './custom-post-type';
import * as headline from './headline';
import * as heatmap from './heatmap';
import * as javascript from './javascript';
import * as menu from './menu';
import * as page from './page';
import * as post from './post';
import * as template from './template';
import * as theme from './theme';
import * as widget from './widget';

import * as wcProduct from './woocommerce-product';
import * as wcBulkSale from './woocommerce-bulk-sale';

/**
 * Function to register core experiments.
 *
 * @example
 * ```js
 * import { registerCoreExperiments } from '@nab/experiment-library';
 *
 * registerCoreExperiments();
 * ```
 */
export const registerCoreExperiments = (): void => {
	[
		// Content experiments
		page,
		post,
		customPostType,
		headline,

		// Heatmap and scrollmap
		heatmap,

		// Site-wide experiments
		template,
		theme,
		widget,
		menu,
		css,
		javascript,

		// WooCommerce experiments
		wcProduct,
		wcBulkSale,
	].forEach( ( experiment ) => {
		if ( ! experiment ) {
			return;
		} //end if
		const { name, settings } = experiment;
		registerExperimentType( name, settings as unknown as ExperimentType );
	} );
};
